<template>
	<view>
		<button 
			class="btn login"
			type="primary"
			open-type="getUserInfo"
			withCredentials = 'true'
			lang="zh_CN"
			@getuserinfo="wxGetUserInfo"
			v-if="isLogin"
		>授权登录</button>
		<button
			class="btn phone"
			type="primary"
			open-type="getPhoneNumber"
			lang="zh_CN"
			@getphonenumber="getPhoneNumber"
			v-else
		>手机号授权</button>
	</view>
</template>

<script>
	export default {
		data() {
			return {
				isLogin:true,
				params:{
					encryptedData:'',
					iv:'',
					sessionKey:'',
					phone:''
				},
				openid:'',
				session_key:''
			};
		},
		methods:{
			wxGetUserInfo(){
				uni.showLoading({
					title:'拉取授权中...'
				});
				let self = this;
				// self.login()
				uni.getUserInfo({
					provider:'weixin',
					success:function(infoRes) {
						self.params.encryptedData = infoRes.encryptedData
						self.params.iv = infoRes.iv
						console.log("获取到的用户信息为:",infoRes)
						uni.removeStorageSync('user-deny')
						uni.hideLoading()
						self.login()
					},
					fail:function(fail) {
						uni.setStorageSync('use-deny',1)
						self.back()
					}
				})
			},
			login(encryptedData,iv){
				let self = this;
				uni.login({
					provider:'weixin',
					success:function(loginRes){
						console.log("获取到的code值为:",loginRes)
						if(loginRes.errMsg.indexOf('ok')!=-1){
							console.log("可以获取到的code的值----",loginRes.code)
							self.getOpenId(loginRes.code)
						}else{
							uni.showToast({
								title:'获取code失败',
								duration:2000,
								icon:'none'
							})
						}
					},
					fail:function(){
						uni.showToast({
							title:'登录失败',
							duration:2000,
							icon:'none'
						})
						self.back()
					}
				})
			},
			getOpenId(code){
				let self = this
				let params = {
					appid:'wx7a1d8a24207f3b32',
					secret:'3f61e642ce662c5b07c5816eed128751',
					js_code:code
				}
				uni.request({
					url:`https://api.weixin.qq.com/sns/jscode2session?appid=${params.appid}&secret=${params.secret}&js_code=${params.js_code}&grant_type=authorization_code`,
					success:function(res){
						self.openid = res.data.openid
						self.session_key = res.data.session_key
						self.params.sessionKey = res.data.session_key
						self.isLogin = false
						console.log("获取到的openid为:",self.openid)
						console.log("获取到的秘钥信息为:",res)
						uni.showToast({
							title:'正在进行手机号授权',
							duration:2000,
							icon:'none'
						})
					},
					fail:(err)=>{
						uni.showToast({
							title:'拉取手机号授权失败',
							duration:2000,
							icon:'none'
						})
						self.back()
					}	
				})
			},
			getPhoneNumber(e){
				let self = this
				console.log("获取到的手机号加密信息为:",e)
				uni.showLoading({
					title:'登录中...'
				});
				if(e.detail.errMsg != "getPhoneNumber:fail user deny"){
					console.log(e)
				}else{
					uni.setStorageSync('user-deny',1)
					self.back()
				}
			}
		},
	}
</script>

<style lang="stylus">

